.review{
    position:relative;
    font-family: 'Poppins', sans-serif;
    width:100%;
    padding:1em 1em 5em;
    background: #F0F2F0;  
    height:850px;
}
.review::after{
    content:'';
    position:absolute;
    background-color:#F0F2F0;
    top:-0.5%;
    left:0;
    height:10px;
    width:100%;
}
.review__head{
    position:relative;
    color: #1c1f2e;
    font-family: 'IBM Plex Sans Thai', sans-serif;
    font-size: 2.5rem;
    border-left: 7px solid #00bfa6;
    margin-bottom: .25em;
    padding-left: .125em;
}
.review__head::after{
    content:'What they talk about us';
    padding-left:.65em;
    position:absolute;
    font-size:.95rem;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    top:-7%;
    left:0;
    color:#6d776d;
}
.reviewCard{
    position:relative;
    width:100%;
    max-width: 350px;
    margin:3em auto;
    border-radius:15px;
    padding-bottom:1em;
    background-color:white;
    text-align: center;
    color:black;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.reviewCard__arrowL,.reviewCard__arrowR{
    position:absolute;
    top:230px;
    font-size:2rem;
    cursor:pointer;
    color:#3a353580;
}

.reviewCard__arrowL{
    left:-4%;
}
.reviewCard__arrowR{
    right:-4%;
}
.reviewCard__img{
    width:100%;
    min-height:400px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}
.reviewCard__info__name{
    margin-top:.25em;
    font-size:2rem;
}
.reviewCard__info__p{
    padding: .25 .25em;
    color: #9099a9;
    font-weight: 500;
}
.unactive{
    position:absolute;
    top:50%;
    opacity:0;
    transition: -webkit-transform 1s ease;
    transition: transform 1s ease;
    transition: transform 1s ease, -webkit-transform 1s ease;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
.active{
    opacity:1;
    transition: all 1s ease;
}
@media screen and (min-width:375px) {
    .review{
            height:1000px;
        }
}
@media screen and (min-width:700px) {
    .reviewCard{
        max-width: 400px;
    }
}
@media screen and (min-width:768px) {
    .review{
        height:750px;
    }
    .reviewCard{
        display:flex;
        max-width:650px;
        padding-bottom:0;
    }
    .reviewCard__info{
        align-self: center;
    }
    .reviewCard__img{
        max-width:400px;
        max-height:500px;
        border-bottom-left-radius: 15px;
        border-top-right-radius: 0;
    }
    .reviewCard__arrowL{
        left:-2%;
    }
    .reviewCard__arrowR{
        right:-2%;
    }
}

@media screen and (min-width:1440px) {
    .review__head{
    font-size: 3rem;

}
.review__head::after{
    font-size: 1.25rem;
    padding-left:.35em;
    top:-9%;
}
    .reviewCard{
        max-width:1050px;
    }
